import React, { useContext, useState } from "react";
import { UserStateContext } from "../../App";
import { dateToString } from "../../utils";
import * as Api from "../../api";
//mui
import { Box, TextField, Stack, Button } from "@mui/material";
import AdapterDateFns from "@mui/lab/AdapterDateFns";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import DesktopDatePicker from "@mui/lab/DesktopDatePicker";

function CareerAddForm({ portfolioOwnerId, setCareerList, setClickAddBtn }) {
  const [title, setTitle] = useState(""); //    .
  const [startDate, setStartDate] = useState(new Date()); //  (Date type)  .
  const [dueDate, setDueDate] = useState(new Date()); //  (Date type)  .

  const userState = useContext(UserStateContext);
  const userId = userState.user.id;

  // Date YYYY-MM-DD   .

  const onSubmitHandler = async (e) => {
    e.preventDefault();

    // Date type ,   YYYY-MM-DD  .
    const fromDate = dateToString(startDate);
    const toDate = dateToString(dueDate);

    //     id,  , , ,   .
    const uptCareerData = {
      userId,
      title,
      fromDate,
      toDate,
    };

    // career/create POST  .
    await Api.post("careers", uptCareerData);

    // careerlist/id GET        .
    const res = await Api.get("career-lists", portfolioOwnerId);
    setCareerList(res.data.careers);

    //    ProjectAddForm .
    setClickAddBtn(false);
  };

  return (
    <Box component="form" onSubmit={onSubmitHandler} sx={{ mt: 1 }}>
      <LocalizationProvider dateAdapter={AdapterDateFns}>
        <Stack direction="row" spacing={2}>
          <DesktopDatePicker
            label=""
            inputFormat={"yyyy-MM-dd"}
            mask={"____-__-__"}
            value={startDate}
            maxDate={dueDate}
            onChange={(date) => setStartDate(date)}
            renderInput={(params) => <TextField {...params} />}
          />
          <DesktopDatePicker
            label=""
            inputFormat={"yyyy-MM-dd"}
            mask={"____-__-__"}
            value={dueDate}
            minDate={startDate}
            onChange={(date) => setDueDate(date)}
            renderInput={(params) => <TextField {...params} />}
          />
        </Stack>
      </LocalizationProvider>
      <Stack sx={{ mt: 2 }}>
        <TextField
          required
          label=" "
          defaultValue={title}
          onChange={(e) => setTitle(e.target.value)}
        />
      </Stack>
      <Stack
        direction="row"
        spacing={2}
        sx={{ mt: 2, justifyContent: "center" }}
      >
        <Button variant="contained" type="submit" sx={ButtonStyle.confirm} disableElevation disableRipple>
          
        </Button>{" "}
        <Button
          type="reset"
          onClick={() => setClickAddBtn(false)}
          variant="outlined"
          sx={ButtonStyle.cancel}
        >
          
        </Button>{" "}
      </Stack>
    </Box>
  );
}

export default CareerAddForm;

const ButtonStyle = {
  confirm : { bgcolor: '#D0CE7C', color: '#31311C',
':hover': {
  bgcolor: '#b1b068',
  color: 'white',
}
},
  cancel: { border: 'solid 1px #db3f2b', color: '#db3f2b', 
':hover': {
  bgcolor: '#bd3421',
  color: 'white',
  border: '0px'
}
},
}