import React, { useState } from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Stack,
} from "@mui/material";

//checkDeleteComplete     
export default function AlertDialog({ checkDeleteComplete }) {
  const [open, setOpen] = useState(true);

  //
  const handleClickDelete = () => {
    handleClose();
    checkDeleteComplete(true);
  };
  //
  const handleClickCancel = () => {
    handleClose();
    checkDeleteComplete(false);
  };
  //   
  const handleClose = () => {
    setOpen(false);
    checkDeleteComplete(false);
  };

  return (
    <div>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle> </DialogTitle>
        <DialogContent>
          <DialogContentText> ?</DialogContentText>
        </DialogContent>
        <DialogActions sx={{ justifyContent: "center", mb: 2 }}>
          <Stack direction="row" spacing={2} sx={{ height: "35px" }}>
            <Button onClick={handleClickDelete} sx={ButtonStyle.confirm}>
              
            </Button>
            <Button onClick={handleClickCancel} sx={ButtonStyle.cancel}>
              
            </Button>
          </Stack>
        </DialogActions>
      </Dialog>
    </div>
  );
}

const ButtonStyle = {
  confirm: {
    bgcolor: "#D0CE7C",
    color: "#31311C",
    ":hover": {
      bgcolor: "#b1b068",
      color: "white",
    },
  },
  cancel: {
    border: "solid 1px #db3f2b",
    color: "#db3f2b",
    ":hover": {
      bgcolor: "#bd3421",
      color: "white",
      border: "0px",
    },
  },
};
