import React, { useState, useContext, useRef } from "react";
import _ from "lodash";
import {
  Box,
  TextField,
  Stack,
  FormControlLabel,
  Radio,
  RadioGroup,
  Button,
  Chip,
} from "@mui/material";
import SchoolIcon from "@mui/icons-material/School";
import * as Api from "../../api";
import { UserStateContext } from "../../App";
import { getEducationApi } from "../../utils";

function EducationEditForm({ education, setEducations, setClickEditBtn }) {
  const userState = useContext(UserStateContext); //     .

  const [school, setSchool] = useState(education.school); //    .
  const [major, setMajor] = useState(education.major); //   .
  const [educationStatus, setEducationStatus] = useState(education.position); // /   .
  const [schoolOpt, setSchoolOpt] = useState(null); //      .

  const statusArr = ["", "", "", ""]; // postion  .
  const userId = userState.user.id; //     .

  // radio button   position .
  const RadioBtnClickHandler = (e, value) => {
    setEducationStatus(value);
  };

  // submit event handler .
  const onSubmitHandler = async (e) => {
    e.preventDefault();

    //    , , /   .
    const dataToSubmit = {
      school,
      major,
      position: educationStatus,
    };

    // educations PUT    .
    await Api.put(`educations/${education.id}`, dataToSubmit);

    // education-lists/id GET        .
    const { data } = await Api.get("education-lists", userId);
    setEducations(data.educations);

    setClickEditBtn(false);
  };

  const sendQuery = async (word) => {
    //     api .
    const api = getEducationApi(word);

    //  open api  GET  .
    const res = await api.get();

    //   3     schoolOpt  .
    const data = res.data.dataSearch.content;

    const opt = data.map((v) => v.schoolName);
    setSchoolOpt(opt);
  };

  // 200ms    .
  const delayedSearchWord = useRef(
    _.debounce((q) => sendQuery(q), 200),
  ).current;

  const schoolChangeHandler = (e) => {
    setSchool(e.target.value);
    delayedSearchWord(e.target.value);
  };

  return (
    <Box component="form" onSubmit={onSubmitHandler} sx={{ mt: 1 }}>
      <Stack spacing={2}>
        <TextField
          required
          label=" "
          onChange={(e) => schoolChangeHandler(e)}
          value={school}
        />
        {schoolOpt && (
          <Stack direction="row" spacing={1}>
            {schoolOpt.map((name, i) => (
              <Chip
                key={"schoolOpt" + i}
                icon={<SchoolIcon />}
                label={name}
                color="primary"
                variant="outlined"
                size="small"
                onClick={(e) => setSchool(e.target.innerText)}
              />
            ))}
          </Stack>
        )}
        <TextField
          required
          label=""
          onChange={(e) => setMajor(e.target.value)}
          value={major}
        />
      </Stack>
      <Stack direction="row" spacing={2} sx={{ mt: 2 }}>
        <RadioGroup
          name="radio-buttons-group"
          row
          value={educationStatus}
          onChange={RadioBtnClickHandler}
        >
          {statusArr.map((item, i) => (
            <FormControlLabel
              key={"position" + i}
              control={<Radio />}
              label={item}
              value={item}
            />
          ))}
        </RadioGroup>
      </Stack>
      <Stack
        direction="row"
        spacing={2}
        sx={{ mt: 2, justifyContent: "center" }}
      >
        <Button variant="contained" type="submit" sx={ButtonStyle.confirm} disableElevation disableRipple>
          
        </Button>{" "}
        <Button
          type="reset"
          onClick={() => setClickEditBtn(false)}
          variant="outlined"
          sx={ButtonStyle.cancel}
        >
          
        </Button>{" "}
      </Stack>
    </Box>
  );
}

export default EducationEditForm;

const ButtonStyle = {
  confirm : { bgcolor: '#D0CE7C', color: '#31311C',
':hover': {
  bgcolor: '#b1b068',
  color: 'white',
}
},
  cancel: { border: 'solid 1px #db3f2b', color: '#db3f2b', 
':hover': {
  bgcolor: '#bd3421',
  color: 'white',
  border: '0px'
}
},
}
