import React, { useState, useContext } from "react";
import { Button, Grid, IconButton, Menu, MenuItem } from "@mui/material";
import MoreHorizIcon from "@mui/icons-material/MoreHoriz";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import * as Api from "../../api";
import { UserStateContext } from "../../App";
import AlertDialog from "../utils/AlertDialog";

function EducationCard({
  education,
  setClickEditBtn,
  isEditable,
  setEducations,
}) {
  const userState = useContext(UserStateContext); //     .

  const [anchorEl, setAnchorEl] = useState(null); // Menu Element .
  const [isOpen, setIsOpen] = useState(null); // Menu Element Open  .
  const [isDeleting, setIsDeleting] = useState(false); //   .

  const userId = userState.user.id; //     .

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
    setIsOpen(Boolean(event.currentTarget));
  };
  const handleClose = () => {
    setAnchorEl(null);
    setIsOpen(false);
  };

  const DelBtnClickHandler = async (isDeleting) => {
    if (isDeleting) {
      // educations DELETE    .
      try {
        await Api.delete("educations", education.id);

        // education-lists/id GET        .
        const { data } = await Api.get("education-lists", userId);
        setEducations(data.educations);
      } catch (err) {
        setEducations([]);
      }
    }
  };

  return (
    <Grid container spacing={2}>
      <Grid item xs={10}>
        <p style={{ marginBottom: "5px" }}>{education.school}</p>
        <p className="text-muted">
          {education.major} ({education.position})
        </p>
      </Grid>
      <Grid item xs={2}>
        {isEditable && (
          <>
            <IconButton onClick={handleClick} sx={{ float: "right", mb: 2 }}>
              <MoreHorizIcon />
            </IconButton>
            {isOpen && (
              <Menu anchorEl={anchorEl} open={isOpen} onClose={handleClose}>
                <MenuItem onClick={handleClose}>
                  <Button
                    onClick={() => setClickEditBtn((cur) => !cur)}
                    startIcon={<EditIcon />}
                  >
                    
                  </Button>
                </MenuItem>
                <MenuItem onClick={handleClose}>
                  <Button
                    color="error"
                    startIcon={<DeleteIcon />}
                    onClick={() => setIsDeleting(true)}
                  >
                    
                  </Button>
                </MenuItem>
              </Menu>
            )}
            {isDeleting && (
              <AlertDialog checkDeleteComplete={DelBtnClickHandler} />
            )}
          </>
        )}
      </Grid>
    </Grid>
  );
}

export default EducationCard;
