import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import {
  Avatar,
  Button,
  TextField,
  Card,
  Container,
  Typography,
} from "@mui/material";

import * as Api from "../../api";
import AlertError from "../utils/AlertError";

function RegisterForm() {
  const navigate = useNavigate();

  //useState email  .
  const [email, setEmail] = useState("");
  //useState password  .
  const [password, setPassword] = useState("");
  //useState confirmPassword  .
  const [confirmPassword, setConfirmPassword] = useState("");
  //useState name  .
  const [name, setName] = useState("");
  const [errorMessage, setErrorMessage] = useState(null); //   .

  const [isEmailValid, setIsEmailValid] = useState(false); //    boolean  .
  const [isPasswordValid, setIsPasswordValid] = useState(false); //    boolean  .
  const [isPasswordSame, setIsPasswordSame] = useState(false); //     boolean  .
  const [isNameValid, setIsNameValid] = useState(false); //    boolean  .
  const [isFormValid, setIsFormVaild] = useState(false); //    boolean  .

  // abc@example.com  regex  .
  const validateEmail = (email) => {
    const reg_email =
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return reg_email.test(email.toLowerCase());
  };

  //  4      .
  useEffect(() => {
    setIsFormVaild(
      isEmailValid && isPasswordValid && isPasswordSame && isNameValid,
    );
  }, [isEmailValid, isPasswordValid, isPasswordSame, isNameValid]);

  //      
  useEffect(() => {
    if (isFormValid) {
      setErrorMessage(null);
    }
  }, [isFormValid]);

  //   
  useEffect(() => {
    setIsEmailValid(validateEmail(email));
  }, [email]);

  //   
  useEffect(() => {
    setIsPasswordValid(password.length >= 4);
  }, [password]);

  //    
  useEffect(() => {
    setIsPasswordSame(password === confirmPassword);
  }, [confirmPassword]);

  //   
  useEffect(() => {
    setIsNameValid(name.length >= 2);
  }, [name]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (isFormValid) {
      try {
        // "users/register"  post.
        await Api.post("users/register", {
          email,
          password,
          name,
        });

        //   .
        navigate("/login");
      } catch (err) {
        setErrorMessage(err.response.data.error.message);
      }
    } else {
      emailInputChecker();
      passwordInputChecker();
      pwdconfirmInputChecker();
      nameInputChecker();
    }
  };

  const emailInputChecker = () => {
    if (!isEmailValid) {
      //   
      setErrorMessage("   .");
    } else {
      setErrorMessage(null);
    }
  };
  const passwordInputChecker = () => {
    if (!isPasswordValid) {
      //   
      setErrorMessage(" 4   .");
    } else {
      setErrorMessage(null);
    }
  };
  const pwdconfirmInputChecker = () => {
    if (!isPasswordSame) {
      //   
      setErrorMessage("  .");
    } else {
      setErrorMessage(null);
    }
  };

  const nameInputChecker = () => {
    if (!isNameValid) {
      //   
      setErrorMessage(" 2   .");
    } else {
      setErrorMessage(null);
    }
  };

  return (
    <Container component="main" maxWidth="xs">
      <Card
        sx={{
          marginTop: 8,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          backgroundColor: "white",
          padding: 2,
          borderRadius: 2,
        }}
      >
        <Avatar
          src="/logo.png"
          variant="square"
          alt="logo"
          sx={{ width: 128, height: 128, mb: 0 }}
        />
        <Typography sx={{ fontSize: "20px" }}></Typography>

        <TextField
          margin="normal"
          name="email"
          label=" "
          fullWidth
          autoComplete="email"
          required
          value={email}
          onBlur={emailInputChecker}
          onChange={(e) => setEmail(e.target.value)}
        />

        <TextField
          margin="normal"
          name="password"
          label=""
          type="password"
          fullWidth
          autoComplete="off"
          required
          value={password}
          onBlur={passwordInputChecker}
          onChange={(e) => setPassword(e.target.value)}
        />

        <TextField
          margin="normal"
          name="confirmpassword"
          label=" "
          type="password"
          fullWidth
          autoComplete="off"
          required
          value={confirmPassword}
          onBlur={pwdconfirmInputChecker}
          onChange={(e) => setConfirmPassword(e.target.value)}
        />
        <TextField
          margin="normal"
          name="name"
          label=""
          type="text"
          fullWidth
          autoComplete="off"
          required
          value={name}
          onBlur={nameInputChecker}
          onChange={(e) => setName(e.target.value)}
        />

        {errorMessage && <AlertError message={errorMessage} />}

        <Button
          type="submit"
          name="LOGIN"
          fullWidth
          variant="contained"
          sx={{ mt: 3, mb: 1 }}
          disabled={!isFormValid}
          onClick={handleSubmit}
        >
          
        </Button>

        <Button variant="text" onClick={() => navigate("/login")}>
          
        </Button>
      </Card>
    </Container>
  );
}

export default RegisterForm;
