import React, { useState, useEffect } from "react";
import AwardCard from "./AwardCard";
import Award from "./Award";
import * as Api from "../../api";
//mui
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Card,
  Typography,
} from "@mui/material";

//   AwardCard  .
function Awards({ portfolioOwnerId, isEditable }) {
  const [awardLists, setAwardLists] = useState([]);

  //   ID ,      .
  useEffect(
    () =>
      Api.get("award-lists", portfolioOwnerId)
        .then((res) => {
          setAwardLists(res.data.awards);
        })
        .catch((err) => setAwardLists([])),
    [portfolioOwnerId],
  );

  return (
    <Card sx={{ marginBottom: "20px" }}>
      <Accordion defaultExpanded={true} sx={{ boxShadow: 0 }}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography sx={{
                  fontFamily: "Elice Digital Baeum",
                  fontSize: "24px",
                  color: "#616161",
                  fontWeight: 500,
                }}></Typography>
        </AccordionSummary>
        <AccordionDetails>
          {awardLists.map((award) => (
            <Award
              key={award.id}
              award={award}
              isEditable={isEditable}
              setAwardLists={setAwardLists}
            />
          ))}
        </AccordionDetails>
      </Accordion>
      <AwardCard setAwardLists={setAwardLists} isEditable={isEditable} />
    </Card>
  );
}

export default Awards;
