import React, { useState } from "react";
import { Button, Grid, IconButton, Menu, MenuItem } from "@mui/material";
import MoreHorizIcon from "@mui/icons-material/MoreHoriz";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import * as Api from "../../api";
import AlertDialog from "../utils/AlertDialog";

function ProjectCard({ project, setClickEditBtn, isEditable, setProjects }) {
  const [anchorEl, setAnchorEl] = useState(null); // Menu Element .
  const [isOpen, setIsOpen] = useState(null); // Menu Element Open  .
  const [isDeleting, setIsDeleting] = useState(false); //   .

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
    setIsOpen(Boolean(event.currentTarget));
  };
  const handleClose = () => {
    setAnchorEl(null);
    setIsOpen(false);
  };

  const DelBtnClickHandler = async (isDeleting) => {
    if (isDeleting) {
      // projects DELETE    .
      await Api.delete("projects", project.id);

      // project-lists/id GET        .
      const res = await Api.get("project-lists", project.user.id);
      setProjects(res.data.projects);
    }
  };

  return (
    <Grid container spacing={2}>
      <Grid item xs={10}>
        <p style={{ marginBottom: 0 }}>{project.title}</p>
        <p className="text-muted">
          {project.description} <br />
          {project.from} ~ {project.to}
        </p>
      </Grid>
      <Grid item xs={2}>
        {isEditable && (
          <>
            <IconButton onClick={handleClick} sx={{ float: "right", mb: 2 }}>
              <MoreHorizIcon />
            </IconButton>
            {isOpen && (
              <Menu anchorEl={anchorEl} open={isOpen} onClose={handleClose}>
                <MenuItem onClick={handleClose}>
                  <Button
                    onClick={() => setClickEditBtn((cur) => !cur)}
                    startIcon={<EditIcon />}
                  >
                    
                  </Button>
                </MenuItem>
                <MenuItem onClick={handleClose}>
                  <Button
                    color="error"
                    startIcon={<DeleteIcon />}
                    onClick={() => setIsDeleting(true)}
                  >
                    
                  </Button>
                </MenuItem>
              </Menu>
            )}
            {isDeleting && (
              <AlertDialog checkDeleteComplete={DelBtnClickHandler} />
            )}
          </>
        )}
      </Grid>
    </Grid>
  );
}

export default ProjectCard;
