import React, { useState, useContext } from "react";
import * as Api from "../../api";
import { UserStateContext } from "../../App";

import { Box, TextField, Stack, Button } from "@mui/material";

//   , { }, {   state}, {  }  props .
function AwardEditForm({ award, setIsEditing, setAwardLists }) {
  //   ,   userid .
  const userState = useContext(UserStateContext);
  const userId = userState.user.id;

  // ,  state .
  const [awardTitle, setAwardTitle] = useState(award.title);
  const [awardDetail, setAwardDetail] = useState(award.description);

  //    ,    .
  const editSubmitHandler = async (e) => {
    e.preventDefault();

    const edtAwardData = {
      title: awardTitle,
      description: awardDetail,
    };

    await Api.put(`awards/${award.id}`, edtAwardData);

    const editData = await Api.get("award-lists", userId);
    setAwardLists(editData.data.awards);
    setIsEditing(false);
  };

  return (
    <Box
      component="form"
      onSubmit={editSubmitHandler}
      sx={{ mt: 1, width: 400 }}
    >
      <Stack spacing={2}>
        <TextField
          required
          label=" "
          onChange={(e) => setAwardTitle(e.target.value)}
          defaultValue={awardTitle}
        />
        <TextField
          required
          label=" "
          onChange={(e) => setAwardDetail(e.target.value)}
          defaultValue={awardDetail}
        />
      </Stack>
      <Stack
        direction="row"
        spacing={2}
        sx={{ mt: 2, justifyContent: "center" }}
      >
        <Button type="submit" sx={ButtonStyle.confirm} disableElevation disableRipple >
          
        </Button>{" "}
        <Button
          variant="outlined"
          type="reset"
          sx={ButtonStyle.cancel}
          onClick={() => setIsEditing(false)}
        >
          
        </Button>{" "}
      </Stack>
    </Box>
  );
}

export default AwardEditForm;

const ButtonStyle = {
  confirm : { bgcolor: '#D0CE7C', color: '#31311C',
':hover': {
  bgcolor: '#b1b068',
  color: 'white',
}
},
  cancel: { border: 'solid 1px #db3f2b', color: '#db3f2b', 
':hover': {
  bgcolor: '#bd3421',
  color: 'white',
  border: '0px'
}
},
}