import React, { useEffect, useContext, useState, useRef } from "react";
import _ from "lodash";
import { useNavigate } from "react-router-dom";
import {
  Container,
  Grid,
  Input,
  Box,
  InputAdornment,
  Typography,
} from "@mui/material";
import PersonSearchIcon from "@mui/icons-material/PersonSearch";

import * as Api from "../../api";
import UserNetworkCard from "./UserNetworkCard";
import { UserStateContext } from "../../App";

function Network() {
  const navigate = useNavigate();
  const userState = useContext(UserStateContext);

  const [users, setUsers] = useState([]); //   .
  const [searchWord, setSearchWord] = useState(""); //  .
  const [isFound, setIsFound] = useState(true); //    .

  useEffect(() => {
    //    user null,   .
    if (!userState.user) {
      navigate("/login");
      return;
    }
    // "users"  GET  , users response data .
    Api.get("users").then((res) => {
      setUsers(res.data.users);
    });
  }, [userState, navigate]);

  const sendQuery = async (word) => {
    try {
      if (word.length === 0) {
        const { data } = await Api.get("users");
        setUsers(data.users);
        setIsFound(true);
      } else {
        const { data } = await Api.get("users/search", word);
        if (data.users.length > 0) {
          setUsers(data.users);
          setIsFound(true);
        } else {
          setIsFound(false);
        }
      }
    } catch (e) {
      console.log(e);
    }
  };

  // 200ms     .
  const delayedSearchWord = useRef(
    _.debounce((q) => sendQuery(q), 200),
  ).current;

  const inputChangeHandler = (e) => {
    setSearchWord(e.target.value);
    delayedSearchWord(e.target.value);
  };


  return (
    <Container sx={{ py: 8 }} maxWidth="md">
      <Box
        sx={{
          display: "flex",
          justifyContent: "flex-end",
          mb: 3,
          fontFamily: "Elice Digital Baeum",
          fontSize: "10px",
          color: "#616161",
          fontWeight: 500,
        }}
      >
        <Input
          variant="outlined"
          startAdornment={
            <InputAdornment position="start">
              <PersonSearchIcon />
            </InputAdornment>
          }
          value={searchWord}
          onChange={(e) => inputChangeHandler(e)}
          placeholder=",   "
          style={{ width: "30%" }}
        />
      </Box>
      <Grid container spacing={4}>
        {isFound ? (
          users.map((user) => <UserNetworkCard key={user.id} user={user} />)
        ) : (
          <Typography>   </Typography>
        )}
      </Grid>
    </Container>
  );
}

export default Network;
