import React, { useState, useEffect } from "react";
import EducationAddForm from "./EducationAddForm";
import Education from "./Education";
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Card,
  CardContent,
  Typography,
  IconButton,
  Box,
  Dialog,
  DialogTitle,
  DialogContent,
} from "@mui/material";
import AddCircleRoundedIcon from "@mui/icons-material/AddCircleRounded";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import * as Api from "../../api";

function Educations({ portfolioOwnerId, isEditable }) {
  const [educations, setEducations] = useState(null); //    .
  const [clickAddBtn, setClickAddBtn] = useState(false); //      .

  useEffect(() => {
    // "education-lists/id"  GET  , educations response data .
    Api.get("education-lists", portfolioOwnerId)
      .then((res) => {
        setEducations(res.data.educations);
      })
      .catch((err) => setEducations([]));
  }, [portfolioOwnerId]);

  return (
    <Card sx={{ marginBottom: "20px" }}>
      <Accordion defaultExpanded={true} sx={{ boxShadow: 0 }}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography sx={{
                  fontFamily: "Elice Digital Baeum",
                  fontSize: "24px",
                  color: "#616161",
                  fontWeight: 500,
                }}></Typography>
        </AccordionSummary>
        <AccordionDetails>
          {educations &&
            educations.map((edu) => (
              <Education
                key={edu.id}
                education={edu}
                setEducations={setEducations}
                isEditable={isEditable}
              />
            ))}
        </AccordionDetails>
      </Accordion>
      {isEditable && (
        <CardContent>
          <Box sx={{ display: "flex", justifyContent: "center" }}>
            <IconButton
              style={{ color: "#C7A27C" }}
              aria-label="add-education"
              onClick={() => setClickAddBtn((cur) => !cur)}
            >
              <AddCircleRoundedIcon sx={{ width: "38px", height: "38px" }} />
            </IconButton>
          </Box>
          {clickAddBtn && (
            <Dialog
              open={clickAddBtn}
              onClose={() => setClickAddBtn((cur) => !cur)}
            >
              <DialogTitle> </DialogTitle>
              <DialogContent>
                <EducationAddForm
                  portfolioOwnerId={portfolioOwnerId}
                  setClickAddBtn={setClickAddBtn}
                  setEducations={setEducations}
                />
              </DialogContent>
            </Dialog>
          )}
        </CardContent>
      )}
    </Card>
  );
}

export default Educations;
