import React, { useState, useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import * as Api from "../../api";
import { DispatchContext } from "../../App";
import {
  Avatar,
  Button,
  TextField,
  Card,
  Container,
  Typography,
  Grid,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText,
  Snackbar,
  Alert,
  Stack,
} from "@mui/material";

import AlertError from "../utils/AlertError";

const LoginForm = () => {
  const navigate = useNavigate();
  const dispatch = useContext(DispatchContext);

  const [email, setEmail] = useState(""); //useState email  .
  const [password, setPassword] = useState(""); //useState password  .
  const [errorMessage, setErrorMessage] = useState(null); //   .
  const [isOpenDialog, setIsOpenDialog] = useState(false); //      .
  const [sendMailSucc, setSendMailSucc] = useState(false); //     .
  const [isEmailValid, setIsEmailValid] = useState(false); //    boolean  .
  const [isPasswordValid, setIsPasswordValid] = useState(false); //    boolean  .
  const [isFormValid, setIsFormVaild] = useState(false); //    boolean  .

  //  abc@example.com  regex  .
  const validateEmail = (email) => {
    const reg_email =
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return reg_email.test(email.toLowerCase());
  };

  //  2      .
  useEffect(() => {
    setIsFormVaild(isEmailValid && isPasswordValid);
  }, [isEmailValid, isPasswordValid]);

  //      
  useEffect(() => {
    if (isFormValid) {
      setErrorMessage(null);
    }
  }, [isFormValid]);

  //   
  useEffect(() => {
    setIsEmailValid(validateEmail(email));
  }, [email]);

  //   
  useEffect(() => {
    setIsPasswordValid(password.length >= 4);
  }, [password]);

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      // "users/login"  post.
      const res = await Api.post("users/login", {
        email,
        password,
      });

      //   response data.
      const user = res.data.user;

      // JWT    token.
      const jwtToken = user.token;
      // sessionStorage "userToken"  JWT  .
      sessionStorage.setItem("userToken", jwtToken);
      // dispatch      .
      dispatch({
        type: "LOGIN_SUCCESS",
        payload: user,
      });

      //   .
      navigate("/", { replace: true });
    } catch (err) {
      setErrorMessage(err.response.data.error.message);
    }
  };

  const guestLoginBtnClick = async (e) => {
    try {
      // "users/login"  post.
      const res = await Api.post("users/login", {
        email: process.env.REACT_APP_GUEST_ID,
        password: process.env.REACT_APP_GUEST_PW,
      });

      //   response data.
      const user = res.data.user;

      // JWT    token.
      const jwtToken = user.token;
      // sessionStorage "userToken"  JWT  .
      sessionStorage.setItem("userToken", jwtToken);
      // dispatch      .
      dispatch({
        type: "LOGIN_SUCCESS",
        payload: user,
      });

      //   .
      navigate("/", { replace: true });
    } catch (err) {
      setErrorMessage(err.response.data.error.message);
    }
  };

  //  Email    .
  const sendMailHandler = async (e) => {
    try {
      await Api.post("users/password", {
        email,
      });

      setSendMailSucc(true);
      setIsOpenDialog(false);
    } catch (err) {
      setErrorMessage(err.response.data.error.message);
    }
  };

  const emailInputChecker = () => {
    if (!isEmailValid) {
      //   
      setErrorMessage("   .");
    } else {
      setErrorMessage(null);
    }
  };
  const passwordInputChecker = () => {
    if (!isPasswordValid) {
      //   
      setErrorMessage(" 4 .");
    } else {
      setErrorMessage(null);
    }
  };

  return (
    <Container component="main" maxWidth="xs">
      <Card
        sx={{
          marginTop: 8,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          backgroundColor: "white",
          padding: 2,
          borderRadius: 2,
          fontFamily: "Elice Digital Baeum",
        }}
      >
        <Avatar
          src="/logo.png"
          variant="square"
          alt="logo"
          sx={{ width: 128, height: 128, mb: 0 }}
        />
        <Typography
          sx={{
            fontFamily: "Elice Digital Baeum",
            fontSize: "22px",
            fontWeight: 600,
          }}
        >
          
        </Typography>

        <TextField
          margin="normal"
          name="email"
          label=" "
          fullWidth
          autoComplete="email"
          required
          value={email}
          onBlur={emailInputChecker}
          onChange={(e) => setEmail(e.target.value)}
        />

        <TextField
          margin="normal"
          name="password"
          label=""
          type="password"
          fullWidth
          autoComplete="current-password"
          required
          value={password}
          onBlur={passwordInputChecker}
          onChange={(e) => setPassword(e.target.value)}
        />

        {errorMessage && <AlertError message={errorMessage} />}

        <Button
          type="submit"
          name="LOGIN"
          fullWidth
          variant="contained"
          sx={{ mt: 3, mb: 1 }}
          disabled={!isFormValid}
          onClick={handleSubmit}
        >
          
        </Button>
        <Button
          type="submit"
          name="GUEST"
          fullWidth
          variant="contained"
          color="secondary"
          sx={{ mb: 2 }}
          onClick={guestLoginBtnClick}
        >
           
        </Button>
        <Grid container>
          <Grid item xs>
            <Button variant="text" onClick={() => setIsOpenDialog(true)}>
               
            </Button>
          </Grid>
          <Grid item>
            <Button variant="text" onClick={() => navigate("/register")}>
              
            </Button>
          </Grid>
        </Grid>
      </Card>

      {isOpenDialog && (
        <Dialog open={isOpenDialog} onClose={() => setIsOpenDialog(false)}>
          <DialogTitle> </DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
               
            </DialogContentText>
            <TextField
              margin="normal"
              name="email"
              label=""
              required
              fullWidth
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            {errorMessage && (
              <Snackbar
                anchorOrigin={{ vertical: "top", horizontal: "center" }}
                open={!sendMailSucc}
                autoHideDuration={6000}
                onClose={() => {
                  setSendMailSucc(false);
                  setErrorMessage(null);
                }}
              >
                <Alert severity="error" sx={{ width: "100%" }}>
                    .     !
                </Alert>
              </Snackbar>
            )}
          </DialogContent>
          <DialogActions sx={{ mt: 2, justifyContent: "center" }}>
            <Stack direction="row" spacing={2}>
              <Button onClick={() => setIsOpenDialog(false)} color="error">
                
              </Button>
              <Button onClick={sendMailHandler}></Button>
            </Stack>
          </DialogActions>
        </Dialog>
      )}

      {sendMailSucc && (
        <Snackbar
          anchorOrigin={{ vertical: "top", horizontal: "center" }}
          open={sendMailSucc}
          autoHideDuration={6000}
          onClose={() => setSendMailSucc(false)}
        >
          <Alert severity="success" sx={{ width: "100%" }}>
              !    
            !
          </Alert>
        </Snackbar>
      )}
    </Container>
  );
};

export default LoginForm;
