import React, { useState, useEffect } from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
  Stack,
} from "@mui/material";
import AlertError from "../utils/AlertError";
import * as Api from "../../api";

function ModifyPassword({ setIsModifyPassword }) {
  const [open, setOpen] = useState(true); // Dialog open  .
  const [password, setPassword] = useState(""); //   .
  const [confirmPassword, setConfirmPassword] = useState(""); //   .
  const [errorMessage, setErrorMessage] = useState(null); //   .
  const [isPasswordValid, setIsPasswordValid] = useState(false); //    boolean  .
  const [isPasswordSame, setIsPasswordSame] = useState(false); //     boolean  .
  const [isFormValid, setIsFormVaild] = useState(false); //    boolean  .

  //     .
  useEffect(() => {
    setIsFormVaild(isPasswordValid && isPasswordSame);
  }, [isPasswordValid, isPasswordSame]);

  //      
  useEffect(() => {
    if (isFormValid) {
      setErrorMessage(null);
    } else {
      if (password !== "" && confirmPassword !== "") {
        passwordInputChecker();
        pwdconfirmInputChecker();
      }
    }
  }, [isFormValid]);

  //     
  useEffect(() => {
    setIsPasswordValid(password.length >= 4);
    setIsPasswordSame(password.length >= 4 && password === confirmPassword);
  }, [password, confirmPassword]);

  // Dialog  
  const handleClose = () => {
    setOpen(false);
    setIsModifyPassword(false);
  };

  //    
  const modifyPasswordBtnClick = async () => {
    try {
      await Api.put("users", {
        password,
      });
      handleClose();
    } catch (err) {
      setErrorMessage(err.response.data.error.message);
    }
  };

  const passwordInputChecker = () => {
    if (!isPasswordValid) {
      //   
      setErrorMessage(" 4   .");
    } else {
      setErrorMessage(null);
    }
  };
  const pwdconfirmInputChecker = () => {
    if (!isPasswordSame) {
      //   
      setErrorMessage("  .");
    } else {
      setErrorMessage(null);
    }
  };
  return (
    <Dialog open={open} onClose={handleClose} fullWidth>
      <DialogTitle> </DialogTitle>
      <DialogContent>
        <Stack spacing={2} sx={{ mt: 2, mb: 2 }}>
          <TextField
            required
            type="password"
            label=" "
            fullWidth
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            onBlur={passwordInputChecker}
          />

          <TextField
            required
            type="password"
            label="  "
            fullWidth
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            onBlur={pwdconfirmInputChecker}
          />
        </Stack>
        {errorMessage && <AlertError message={errorMessage} />}
      </DialogContent>
      <DialogActions sx={{ justifyContent: "center", mb: 2 }}>
        <Stack direction="row" spacing={2} sx={{ height: "35px" }}>
          <Button
            onClick={modifyPasswordBtnClick}
            autoFocus
            size="small"
            disabled={!isFormValid}
            sx={ButtonStyle.confirm}
          >
            
          </Button>
          <Button onClick={handleClose} sx={ButtonStyle.cancel} size="small">
            
          </Button>
        </Stack>
      </DialogActions>
    </Dialog>
  );
}

const ButtonStyle = {
  confirm: {
    bgcolor: "#D0CE7C",
    color: "#31311C",
    ":hover": {
      bgcolor: "#b1b068",
      color: "white",
    },
  },
  cancel: {
    border: "solid 1px #db3f2b",
    color: "#db3f2b",
    ":hover": {
      bgcolor: "#bd3421",
      color: "white",
      border: "0px",
    },
  },
};

export default ModifyPassword;
